import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { FormDataService } from '../data/formData.service';
import { OtherInfo } from '../data/formData.model';
import { NgForm } from '@angular/forms';

@Component ({
    selector: 'mt-wizard-work',
    templateUrl: './other-info.html'
})

export class OtherInfoComponent implements OnInit {
    title = 'Other Info';
    otherInfo: OtherInfo;
    form: any;
    websiteIntegrationRequired = false;

    @ViewChild('otherInfoForm') public otherInfoForm: NgForm;

    files: any;

    certificateTypes = [
        'COR',
        'COI',
        'T&C',
        'ID'
    ];

    pluginTypes = [
        'Woo-Commerce',
        'Shopify',
        'Magento'
    ];

    constructor(
        private router: Router,
        private formDataService: FormDataService) {}

    ngOnInit() {
        this.otherInfo = this.formDataService.getOtherInfo();
        console.log('Other Info feature loaded!');
    }

    save(form: any): boolean {
        console.log('other info', form.value);
        if (!form.valid) {
            return false;
        }

        this.formDataService.setOtherInfo(this.otherInfo);
        return true;
    }

    goToPrevious(form: any) {
        if (this.save(form)) {
            // Navigate to the main contact page
            this.router.navigate(['/mainContact']);
        }
    }

    goToNext(form: any) {

        /*if (this.files != null) {
            console.log('file: ', this.files[0]);
            form.form.get('file-upload').setValue(this.files[0]);
        } else {
            console.log('no files!!');
        }*/

        if (this.save(form)) {
            // Navigate to the result page
            this.router.navigate(['/result']);
        }
    }

    onWebIntegrationRequired($event) {
        this.websiteIntegrationRequired = $event;
        console.log('websiteIntegrationRequired : ' + this.websiteIntegrationRequired);
    }

    setFileForUpload(files) {
        console.log('files', files);
        if (files.length > 0) {
            this.files = files;
        }
    }
}



