import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CopmanyDetailsComponent } from './company-details/company-details.component';
import { MainContactComponent } from './main-contact/main-contact.component';
import { OtherInfoComponent } from './other-info/other-info.component';
import { ResultComponent } from './result/result.component';

import { WorkflowGuard } from './workflow/workflow-guard.service';
import { WorkflowService } from './workflow/workflow.service';


export const appRoutes: Routes = [
    // 1st Route
    { path: 'companyDetails',  component: CopmanyDetailsComponent },
    // 2nd Route
    { path: 'otherInfo',  component: OtherInfoComponent, canActivate: [WorkflowGuard] },
    // 3rd Route
    { path: 'mainContact',  component: MainContactComponent, canActivate: [WorkflowGuard] },
    // 4th Route
    { path: 'result',  component: ResultComponent, canActivate: [WorkflowGuard] },
    // 5th Route
    { path: '',   redirectTo: '/companyDetails', pathMatch: 'full' },
    // 6th Route
    { path: '**', component: CopmanyDetailsComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule],
  providers: [WorkflowGuard]
})

export class AppRoutingModule {}


