import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { CompanyDetails } from '../data/formData.model';
import { FormDataService } from '../data/formData.service';

@Component ({
    selector: 'mt-wizard-personal',
    templateUrl: './company-details.component.html'
})

export class CopmanyDetailsComponent implements OnInit {
    title = 'Company Details';
    companyDetails: CompanyDetails;
    form: any;

    constructor(private router: Router, private formDataService: FormDataService) {
    }

    ngOnInit() {
        this.companyDetails = this.formDataService.getCompanyDetails();
        console.log('Personal feature loaded!');
    }

    save(form: any): boolean {
        console.log('company details', form.value);
        if (!form.valid) {
            return false;
        }

        this.formDataService.setCompanyDetails(this.companyDetails);
        return true;
    }

    goToNext(form: any) {
        if (this.save(form)) {
            // Navigate to the work page
            this.router.navigate(['/mainContact']);
        }
    }
}



