import { Component, OnInit, Input } from '@angular/core';

import { MyFormData } from '../data/formData.model';
import { FormDataService } from '../data/formData.service';
import { Http } from '@angular/http';
import { Router } from '@angular/router';

import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';

@Component ({
    selector: 'mt-wizard-result',
    templateUrl: './result.component.html'
})

export class ResultComponent implements OnInit {

    // The URLs
    private localHostUrl = 'http://localhost/onboarding-backend/dist/excel-generator.php';
    private herkuHostUrl = 'https://merchant-onboarding.herokuapp.com/excel-generator.php';

    title = 'Confirm Details';
    @Input() myFormData: MyFormData;
    isFormValid: boolean = false;

    constructor(
        private formDataService: FormDataService,
        private http: Http,
        private spinner: NgxSpinnerService,
        private router: Router,
        private toastr: ToastrService) {
    }

    ngOnInit() {
        this.myFormData = this.formDataService.getFormData();
        this.isFormValid = this.formDataService.isFormValid();
        console.log('Result feature loaded!');
    }

    submit() {
        this.myFormData = this.formDataService.getFormData();

        this.isFormValid = false;

        console.log('form values', JSON.stringify(this.myFormData));

        const formData = new FormData();
        formData.append('company-details', JSON.stringify(this.myFormData));
        this.formDataService.resetFormData();

        this.spinner.show();
        this.http.post(this.herkuHostUrl, formData)
        .subscribe((data) => {
            console.log('Got some data from backend', data);
            this.spinner.hide();
            this.router.navigate([('/companyDetails')]);
            this.showSuccess();
        }, (error) => {
            console.log('Error', error );
            this.spinner.hide();
            this.showError(error);
        });
    }

    /*startUpload(file: File) {
        if (file != null) {
            console.log(file);
            const formData = new FormData();
            formData.append('file', file);

            this.http.post('http://localhost/onboarding-backend/dist/excel-generator.php', formData)
            .subscribe((data) => {
                console.log('got response from server after uploading certificate', data);
            }, (error) => {
                console.log('There was an error', error);
            });
        }
    }*/

    showSuccess() {
        this.toastr.success('Form Submitted and Email Sent', 'Success');
    }

    showError(msg) {
        this.toastr.error(msg, 'Error');
    }
}



