import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { MainContact } from '../data/formData.model';
import { FormDataService } from '../data/formData.service';

@Component ({
    selector: 'mt-wizard-address',
    templateUrl: './main-contact.component.html'
})

export class MainContactComponent implements OnInit {
    title = 'Main Contact';
    mainContact: MainContact;
    form: any;

    constructor(private router: Router, private formDataService: FormDataService) {
    }

    ngOnInit() {
        this.mainContact = this.formDataService.getMainContact();
        console.log('Main contact feature loaded!');
    }

    save(form: any): boolean {
        console.log('main contact', form.value);
        if (!form.valid) {
            return false;
        }

        this.formDataService.setMainContact(this.mainContact);
        return true;
    }

    goToPrevious(form: any) {
        if (this.save(form)) {
            // Navigate to the work page
            this.router.navigate(['/companyDetails']);
        }
    }

    goToNext(form: any) {
        if (this.save(form)) {
            // Navigate to the result page
            this.router.navigate(['/result']);
        }
    }
}



