export class MyFormData {

    accountType: string = '';
    companyName: string = '';
    tradeName: string = '';
    registrationNumber: string = '';
    companyEmail: string = '';
    websiteUrl: string = '';
    phoneNumber: string = '';
    address1: string = '';
    address2: string = '';
    city: string = '';
    state: string = '';
    country: string = '';
    zip: string = '';
    otherInfo: string = '';
    mainContactFirstName: string = '';
    mainContactLastName: string = '';
    mainContactPosition: string = '';
    mainContactEmail: string = '';
    mainContactMobile: string = '';
    websiteIntegration: boolean = false;
    cmsPlugin: string = '';
    uploadedCertificateType: string = '';
    uploadedFile: File = null;

    clear() {
        this.accountType = '';
        this.companyName = '';
        this.tradeName = '';
        this.registrationNumber = '';
        this.companyEmail = '';
        this.websiteUrl = '';
        this.phoneNumber = '';
        this.address1 = '';
        this.address2 = '';
        this.city = '';
        this.state = '';
        this.country = '';
        this.zip = '';
        this.mainContactFirstName = '';
        this.mainContactLastName = '';
        this.mainContactPosition = '';
        this.mainContactEmail = '';
        this.mainContactPosition = '';
        this.mainContactMobile = '';
        this.websiteIntegration = false;
        this.cmsPlugin = '';
        this.uploadedCertificateType = '';
        this.uploadedFile = null;
    }
}

export class CompanyDetails {
    accountType: string = '';
    companyName: string = '';
    tradeName: string = '';
    registrationNumber: string = '';
    companyEmail: string = '';
    websiteUrl: string = '';
    phoneNumber: string = '';
    address1: string = '';
    address2: string = '';
    city: string = '';
    state: string = '';
    country: string = '';
    zip: string = '';
    otherInfo: string = '';
}

export class MainContact {
    mainContactFirstName: string = '';
    mainContactLastName: string = '';
    mainContactPosition: string = '';
    mainContactEmail: string = '';
    mainContactMobile: string = '';
}

export class OtherInfo {
    websiteIntegration: boolean = false;
    cmsPlugin: string = '';
    uploadedCertificateType: string = '';
    uploadedFile: File = null;
}



